@keyframes addGlassOfWater {
  0% {
    fill: #EBE441;
    transform: scale(1);
    transform-origin: 50% 50%; }
  50% {
    fill: #41a1eb;
    transform: scale(1.1);
    transform-origin: 50% 50%; }
  100% {
    fill: #EBE441;
    transform: scale(1);
    transform-origin: 50% 50%; } }

@keyframes removeGlassOfWater {
  0% {
    fill: #EBE441;
    transform: scale(1);
    transform-origin: 50% 50%; }
  50% {
    fill: #eb4141;
    transform: scale(0.9);
    transform-origin: 50% 50%; }
  100% {
    fill: #EBE441;
    transform: scale(1);
    transform-origin: 50% 50%; } }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

.button, .navigation__add, .navigation__remove {
  width: 256px;
  height: 48px;
  transition: font-size 0.2s ease-in-out; }
  .button:hover, .navigation__add:hover, .navigation__remove:hover {
    font-size: 20px; }

body {
  color: #3657CF;
  background: #ABA62E;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  min-height: 99vh;
  display: grid;
  grid-template-rows: 1fr 32px; }

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px; }
  .footer__link {
    text-decoration: none;
    font-weight: bold;
    color: #3657CF; }

.glass {
  display: flex;
  justify-content: center;
  position: relative;
  margin-bottom: 80px; }
  .glass__water {
    transition: fill 0.3s ease-in-out, transform 0.3s ease-in-out; }
    .glass__water--add {
      animation: addGlassOfWater 0.3s ease-in-out; }
    .glass__water--remove {
      animation: removeGlassOfWater 0.3s ease-in-out; }
  .glass__counter {
    color: #000000;
    font-size: 112px;
    position: absolute; }

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.navigation {
  font-size: 18px;
  line-height: 21px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .navigation__add {
    border-radius: 4px;
    margin-bottom: 16px;
    transition: box-shadow 0.2s ease-in-out; }
    .navigation__add:hover {
      box-shadow: 2px 2px 10px #4a4812; }
  .navigation__remove {
    background-color: #ABA62E;
    border: none;
    transition: text-shadow 0.2s ease-in-out; }
    .navigation__remove:hover {
      text-shadow: 2px 2px 10px #4a4812; }
